.home {
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_image {
    width: 100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}
.home_row {
    display: flex;
    flex-wrap: wrap;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    justify-content: space-evenly;
}
/* .around {
    margin-top: -150px;
} */
.categories {
    margin-top: -100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}
